:root {
  --background-color: #fff;
  --time-function: cubic-bezier(.4, 0, .2, 1);
  --time-duration: 250ms;
  --accent-text-color: #ff6b02;
}

.backdrop {
  z-index: 1;
  transition: opacity var(--time-duration), visibility var(--time-duration);
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
}

.modalA {
  z-index: 2;
  width: 280px;
  min-height: 180px;
  transition: transform var(--time-duration);
  background-image: linear-gradient(to bottom right, #1a1919, #50010e);
  border-radius: 4px;
  padding: 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .modalA {
  transform: translate(-50%, -50%)scale(0);
}

@media screen and (min-width: 1200px) {
  .modalA {
    width: 400px;
    min-height: 300px;
  }
}

.modal__close-btn {
  width: 25px;
  height: 25px;
  fill: #fff;
  cursor: pointer;
  transition: transform var(--time-duration) var(--time-function);
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__close-btn:hover, .modal__close-btn:focus {
  transform: scale(1.2);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
}

